"use client";

import { useEffect, useState, useCallback } from "react";
import api from "@/lib/axios";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

interface AdminOrder {
    id: number;
    reference: string;
    recipient_phone: string;
    status: string;
    cost: number;
    user?: {
        email: string;
    };
    bundle?: {
        name: string;
    };
}

export default function AdminOrdersPage() {
    const [orders, setOrders] = useState<AdminOrder[]>([]);
    const [filter, setFilter] = useState("all");

    const fetchOrders = useCallback(async () => {
        const url = filter === 'all' ? '/admin/orders' : `/admin/orders?status=${filter}`;
        const res = await api.get(url);
        setOrders(res.data.data);
    }, [filter]);

    useEffect(() => {
        Promise.resolve().then(() => fetchOrders());
    }, [fetchOrders]);

    const updateStatus = async (id: number, status: string) => {
        try {
            await api.put(`/admin/orders/${id}`, { status });
            fetchOrders();
        } catch (e) {
            alert("Failed to update status");
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex justify-between">
                <h2 className="text-2xl font-bold">Manage Orders</h2>
                <div className="w-48">
                    <Select onValueChange={setFilter} defaultValue="all">
                        <SelectTrigger>
                            <SelectValue placeholder="Filter Status" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="all">All Statuses</SelectItem>
                            <SelectItem value="pending">Pending</SelectItem>
                            <SelectItem value="processing">Processing</SelectItem>
                            <SelectItem value="completed">Completed</SelectItem>
                            <SelectItem value="failed">Failed</SelectItem>
                        </SelectContent>
                    </Select>
                </div>
            </div>

            <div className="bg-white rounded border">
                <table className="w-full text-sm text-left">
                    <thead className="bg-gray-50 uppercase text-gray-500">
                        <tr>
                            <th className="px-6 py-3">Ref</th>
                            <th className="px-6 py-3">User</th>
                            <th className="px-6 py-3">Bundle</th>
                            <th className="px-6 py-3">Phone</th>
                            <th className="px-6 py-3">Status</th>
                            <th className="px-6 py-3">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                        {orders.map((order) => (
                            <tr key={order.id}>
                                <td className="px-6 py-4">{order.reference}</td>
                                <td className="px-6 py-4">{order.user?.email}</td>
                                <td className="px-6 py-4">{order.bundle?.name}</td>
                                <td className="px-6 py-4">{order.recipient_phone}</td>
                                <td className="px-6 py-4 capitalize">{order.status}</td>
                                <td className="px-6 py-4">
                                    <div className="flex gap-2">
                                        <Button size="sm" variant="outline" onClick={() => updateStatus(order.id, 'completed')}>
                                            Mark Complete
                                        </Button>
                                        <Button size="sm" variant="destructive" onClick={() => updateStatus(order.id, 'failed')}>
                                            Fail
                                        </Button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
